import React from 'react';
import './songview.css'

const SongView =({playlist})=>{
    return(
        <div className={ playlist.id%2===0 ? 'grey playlist-container' : 'white playlist-container' } >
            <div className='setting-equal-width'>
             {playlist.id}.{playlist.songname}
            </div>

            <div className='setting-equal-width'>
            {playlist.author}
            </div>
            
            <div className='setting-equal-width'>
             {playlist.duration}   
            </div>

            <div className='ps-type'>
                {playlist.type}
            </div>

            <div className='setting-equal-width'>

            </div>
            
        </div>
    )
}

export default SongView